* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #fff;
  color: #d91e18;
  font-family: Helvetica, Arial, sans-serif;
  text-align: center;
}

html,
body {
  height: 100%;
}

.hidden {
  display: none;
}

.block {
  display: block;
}

.center {
  margin: 0 auto;
  text-align: center;
}

.symbol {
  fill: white;
}

img {
  max-width: 100%;
  max-height: 100%;
}